<template>

      <a-layout style="background: #f0f2f5;width:100%;height:90%">
        <a-layout-sider width="300" style="background: #FFF;overflow:scroll;height: '100%'">
          <div class="sider-top">
            <router-link to="*" class="header-menu-left">
            <!-- <a-avatar :size="80" icon="user" src="./static/8.jpeg" style="color:rgb(255, 255, 255);" /> -->
          </router-link>
            <img
              height="115px"
              src="../assets/dddb-webEnt/company1.png" alt="">
            <h3 style="color:#0c0c0c;padding-top:20px">{{entInfo.enterName}}</h3>
            <a-divider style="width:80%;margin-left:auto;margin-right:auto;" />
          </div>
          <div class="sider-content" v-infinite-scroll="handleInfiniteOnLoad">
              <h4 style="color:#0c0c0c;padding-bottom:10px"><a-icon type="dashboard" />匹配中的需求</h4>
              <template v-for="need in needList"  style="font-size:14px;color:rgba(255,255,255,0.65)">
                <a-card class="sidecard"
                  :key="need"
                  v-if="need.needsType=='匹配中'"
                  hoverable
                  @click="handleClick(need.id)"
                  style="margin-bottom:20px"
                >
                  <h2 style="color: white">{{need.needsFarmerType}}</h2>
                  <h3 style="color: white">{{need.needsDes}}</h3>
                  <h4 style="color: white">开工时间:&nbsp{{need.needsBeginTime}}</h4>
                  <h4 style="color: white">{{need.needsLocation}}<a-icon style="float:right;margin-top:5px" type="right" /></h4>
                </a-card>
              </template>
              <router-link to="/listpage">
              <!-- <a style="font-size:14px;color:#1890ff;">更多</a> -->
              </router-link>
          </div>
        </a-layout-sider>

        <a-layout-content style="padding-top:100px;min-width: 1000px;" :style="{backgroundImage: 'url(' + img + ')', backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100% '}">
          <a-row type="flex" justify="center" align="middle">
            <a-col  align="right" style="flex-shrink: 1">
              <img height="30px" src="../assets/dddb-webEnt/dec.png" alt="">
            </a-col>
           <a-col  style="font-size: 30px;font-weight: bold;flex-shrink: 0" align="center">
             功能入口</a-col>
            <a-col  align="left" style="flex-shrink: 1">
              <img height="30px" src="../assets/dddb-webEnt/dec.png" alt="">
            </a-col>
          </a-row>
          <br>

          <a-row type="flex" justify="space-between" align="middle" >
            <a-col :span="8" align="center">
              <router-link to="/demand">
                <a-card class="mycard"
                  hoverable
                >
                  <img
                    alt="example"
                    src="../assets/dddb-webEnt/1.png"
                    slot="cover"
                  />
                </a-card>
              </router-link>
            </a-col>
            <a-col :span="8" align="center">
              <router-link to="/listpage">
                <a-card class="mycard"
                  hoverable
                >
                  <img
                    alt="example"
                    src="../assets/dddb-webEnt/2.png"
                    slot="cover"
                  />
                </a-card>
              </router-link>
            </a-col>
            <a-col :span="8" align="center">
              <router-link to="/entProfile">
                <a-card class="mycard"
                  hoverable
                >
                  <img
                    alt="example"
                    src="../assets/dddb-webEnt/3.png"
                    slot="cover"
                  />
                </a-card>
              </router-link>
            </a-col>
          </a-row>
        </a-layout-content>
      </a-layout>

</template>
<script>
import {getEnterpriseInfoById} from '../api/manRequest'
import { getMatchingDemand } from '../api/login';
import ACol from "ant-design-vue/es/grid/Col";

export default {
  components: {ACol},
  data() {
    return {
      entId: Number(localStorage.getItem('entId')),
      needList:[],
      img:require('../assets/dddb-webEnt/A2.1-changecode-bg.png'),
      entInfo:{},
    };
  },
  computed: {

  },
  methods: {
    handleClick (itemId) {
      this.$router.push({
        name: "demanddetail",
        query: {
          id: itemId
        }
      });
    },
    getEnterpriseInfoById(){
      getEnterpriseInfoById(this.entId)
      .then(response => {
        this.entInfo = response.data.data;
      })
      .catch(err => {
        console.log(err);
      })
    },
    getMatchingDemands(){
      getMatchingDemand(this.entId)
      .then(response =>{
        this.needList = response.data.data.needList
      })
      .catch(function (error) {
        console.log(error)
      })
    }
  },
    created(){
      this.$parent.showMenu=true
  },
  mounted() {
    this.getMatchingDemands(),
    this.getEnterpriseInfoById()
  },

};
</script>

<style scoped>
.sider-top{
    text-align: center;
    padding: 20px;
  background-color: white;
}
.sider-content{
    padding-left: 40px;
    padding-bottom: 90px;
  background-color: white;
}
h4{
    color: rgba(0, 0, 0, 0.35)
}

  /*卡片*/
  .mycard{
    /*width:488px;*/
    /*height:407px;*/
    width: 90%;
    height: 100%;
    background:rgba(16,16,16,1);
    border-radius:2px;
    border:1px solid rgba(233,233,233,1);
  }
  .mycard img{
    width: 100%;
    height: 100%;
    /*background-color:rgba(184, 12, 10, 1);*/
    /*padding: 5%;*/
  }
  .mycard h1{
    color: white;
    font-size: 20px;
  }
.sidecard{
  width:213px;
  height:174px;
  background:rgba(144,30,2,1);
  box-shadow:0px 2px 40px 0px rgba(176,176,176,0.5);
  border-radius:5px;
}

</style>



